<template>
  <div>
    <form @submit="addTodo">
      <input
        type="text"
        v-model="title"
        name="title"
        placeholder="Add Todo..."
      />
      <input type="submit" value="Add" class="btn" />
    </form>
  </div>
</template>

<script>
import { v4 } from 'uuid';
export default {
  name: 'AddTodo',
  data() {
    return {
      title: '',
    };
  },
  methods: {
    addTodo(e) {
      e.preventDefault();
      const newTodo = {
        id: v4(),
        title: this.title, //Binded From v-model
        completed: false,
      };
      this.$emit('add-todo', newTodo);
      this.title = '';
    },
  },
};
</script>

<style scoped>
form {
  display: flex;
  width: 250px;
  margin: auto;
  padding: 20px;
}
input[type='text'] {
  flex: 10;
  padding: 5px;
}
input[type='submit'] {
  flex: 2;
}
</style>
